#first_screen {
  font-family: @main-font;
  box-sizing: border-box;
  display: inline-block;
  color: @main_bg;
  display: block;
}
.content {
  text-align: right;
  width: 50%;
  background: @main_content;
  overflow: auto;
  float: right;
  height: 100vh;
  position: relative;
}
.soc {
  display: block;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}


